

























































































































































































import Vue from "vue";
import {mapMutations} from "vuex";

function requestDefaultValues() {
    return {
        name: 'Unidad',
        subject: null,
        schoolLevel: null,
        level: null,
        course: null,
        year: new Date().getFullYear(),
        startDate: new Date().toISOString().substr(0, 10),
        endDate: new Date().toISOString().substr(0, 10),
    }
}

export default Vue.extend({
    name: "RomaUnitModalForm",
    props: {
        value: Boolean,
        unit: {
            type: Object
        },
        request: {
            type: Object,
            default () {
                return requestDefaultValues();
            }
        },
        maxWidth: {
            type: String,
            default: '800'
        },
        overlayOpacity: {
            type: String,
            default: '0.8'
        },
        persistent: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            valid: false,
            loading: false,
            showing: this.value,
            startDateMenu: false,
            endDateMenu: false,
            courses: ['A', 'B', 'C', 'D', 'E', 'F'],
            data: [
                {
                    id: 1,
                    name: 'Básica',
                    subjects: [
                        {name: 'Lenguaje', levels: [1,2,3,4,5]},
                        {name: 'Matemáticas', levels: [1,2,3,4,5,6,7,8]},
                        {name: 'Ciencias Naturales', levels: [1,2,3]},
                        {name: 'Historia, Geografía y Ciencias Sociales', levels: [1,2]},
                    ]
                },
                {
                    id: 2,
                    name: 'Media',
                    subjects: [
                        {name: 'Lenguaje', levels: [1,2,3]},
                        {name: 'Matemáticas', levels: [1,2,3,4,5,6]},
                        {name: 'Historia Geográfica / Ciencias Sociales', levels: [1,2]},
                        {name: 'Ciencias Naturales', levels: [1,2,3]},
                    ]
                }
            ],
        };
    },
    created () {
      // si va a editar
      if (this.unit) {
          this.request = this.unit;
      }
    },
    methods: {
        show (value) {
            this.$emit('input', value);
            this.request = requestDefaultValues();
        },
        ...mapMutations({
            addNewUnit (commit) {
                commit('addNewUnit', this.request);
                this.request = requestDefaultValues();
                this.show(false);
            },
        })
    },
    computed: {
      requesting: {
          get () {
              return this.request;
          },
          set (request) {

          }
      }
    },
});
