

























































import Vue from 'vue';
import { createSimpleTransition } from 'vuetify/src/util/helpers';
import RomaUnit from './RomaUnit';
import RomaUnitModalForm from './RomaUnitModalForm';
import {mapState} from "vuex";

const romaUnitListTransition = createSimpleTransition('unit-list-transition');

Vue.component('my-transition', romaUnitListTransition);

export default Vue.extend({
    name: 'RomaUnitList',
    props: {
        /*value: {
            type: Object
        },*/
    },
    data() {
        return {
            showUnitModalForm: false,
        };
    },
    computed: {
        ...mapState({
            school: state => state.organizer.selectedSchool
        })
    },
    methods: {
        editUnit (unit) {
            this.$refs.unitModalForm.request = unit;
            this.showUnitModalForm = true;
        },
        delUnit (index: number) {
            this.$store.commit('delUnit', index);
        },
    },
    components: {
        RomaUnit,
        RomaUnitModalForm,
    },
});
